
 import axios from "axios";
 import Joi from "joi";
 import React, { useState } from "react"
 import { useNavigate } from "react-router-dom";

  export default function Login(props){
      
   const [errorList, setErrorList] =useState([]);
   let  navigate = useNavigate();
     const [isLoadin, setIsLoading] =useState(false);
       const [error, setError] =useState(''); 
       const [user, setUser] = useState({
        
         email: '' ,
         password: ''
       });
       function getUser(e)
       {
        let myUser = {...user };
        myUser[e.target.name] = e.target.value;
        setUser(myUser);
      
       }
       async function submitLogin(e)
       {
           e.preventDefault();
           setIsLoading(true)
           let validationResult = validateLoginForm(user);
           if (validationResult.error){
             setIsLoading(false)
             setErrorList(validationResult.error.details);
           }
           else{
                 
          let { data} = await axios.post(`https://route-egypt-api.herokuapp.com/signin` , user)
          if(data.message === 'success'){
           localStorage.setItem('userToken' , data.token)
           setIsLoading(false)
           props.getUserData();
           navigate('/home');
          
             localStorage.setItem('token',data.token);
               
             }
          else{
             setError(data.message)
             setIsLoading(false)
           
          }
 
          }
           }
          
       function validateLoginForm(user)
       {
           let schema = Joi.object({
        
           email: Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'],} }),
           password:Joi.string().pattern(/^[A-Z][a-z]{3,8}$/)
          
           });
            return schema.validate(user, {abortEarly: false});
       }
      return(
          <div>
              <h1 className="my-5">Login Now</h1>
               {errorList.map((error , index)=> {
                 if(index === 4){
                   return <div key={index} className="alert alert-danger">password invalid</div>
                 }
                 else{
                  return <div key={index} className="alert alert-danger">{error.message}</div>
                 }
             
             })}
              {error? <div className="alert alert-danger">{error}</div>:'' }
             
              <form className="py4" onSubmit={submitLogin}>
                
                  <label htmlFor="email">email :</label>
                  <input onChange={getUser} type="email" className="form-control my-3" name="email" id="email"/>
 
                  <label htmlFor="password">password :</label>
                  <input onChange={getUser} type="password" className="form-control my-3" name="password" id="password"/>
                  <button className="btn btn-outline-info ">
                  {isLoadin?<i className="fas fa-spinner fa-spin"></i>:'Login'}
                  </button>
              </form>
          </div>
      )
  }
 