

import jwtDecode from 'jwt-decode';
import { useEffect, useState } from 'react'
import { Navigate, Routes ,Route, useNavigate} from 'react-router-dom';
import About from '../About/About';
import Details from '../Details/Details';
import Home from '../Home/Home';
import Login from '../Login/Login';
import Movies from '../Movise/Movise';
import Navbar from '../Navbar/Navbar';
import Notfound from '../Notfound/Notfound';
import People from '../People/People';
import Register from '../Register/Register';
import Tvshows from '../Tvshows/Tvshows';
 
function App() {
 const [userData, setUserData] = useState(null)
 let navigate = useNavigate();
 useEffect(() => {
  if(localStorage.getItem('userToken'))
  {
    getUserData();
  }
 }, [])
 function getUserData()
   {
     let decodedToken = jwtDecode(localStorage.getItem('userToken'));
     setUserData(decodedToken); 
   }
   useEffect(()=> { console.log(userData) } , [userData])
   function logOut() {
     localStorage.removeItem('userToken');
     setUserData(null);
     navigate('/login');
   }
   function ProtectedRoute({ children }) {
     if (!localStorage.getItem('userToken')){
     return <Navigate to='/logen'/>
     }
     else
     {
      return children;
     }
   }
  return (
    < >
       <Navbar userData={userData} logOut={logOut} />
       <div className='container'>
       <Routes>
       <Route path='/' element={<Home/>}></Route>
         <Route path='home' element={ <ProtectedRoute><Home/></ProtectedRoute> }></Route>
         <Route path='moves' element={ <ProtectedRoute><Movies/></ProtectedRoute>  }></Route>
         <Route path='tvshows' element={ <ProtectedRoute><Tvshows/></ProtectedRoute>  }></Route>
         <Route path='login' element={<Login getUserData={getUserData} />}></Route>
         <Route path='regiser' element={<Register/>}></Route>
         <Route path='about' element={ <ProtectedRoute><About/> </ProtectedRoute> }></Route>
         <Route path='people' element={<ProtectedRoute><People/></ProtectedRoute>  }></Route>
         <Route path='details' element={<ProtectedRoute><Details/></ProtectedRoute>  }></Route>
         <Route path='*' element={<Notfound/>}></Route>
       </Routes>
       </div>
    </>
  );
}

export default App;
