import React from "react";
import { Link } from 'react-router-dom';
import  styles from './Navbar.module.css'
 
export default function Navbar(props){
  
return(
  
  <nav className={`navbar navbar-expand-lg navbar-light ${styles.navBg}`}>
  <div className="container-fluid">
    <a className="navbar-brand" href="#">Noxe</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
     
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
       {
         props.userData ? 
         <>
            <li className="nav-item">
        <Link className="nav-link" to="home">Home</Link>
       </li>
       <li className="nav-item">
        <Link className="nav-link" to="moves">Movies</Link>
       </li>
       <li className="nav-item">
        <Link className="nav-link" to="tvshows">Tv shows</Link>
       </li>
       <li className="nav-item">
        <Link className="nav-link" to="people">People</Link>
       </li>
       <li className="nav-item">
        <Link className="nav-link" to="about">About</Link>
       </li>
       
         </> :''

       }
    
        </ul>

       <ul className="list-unstyled d-flex auth-links m-0 ms-auto">
         <div className="social-linkes d-flex align-items-center">
          <i className="fab fa-facebook"></i>
          <i className="fab fa-instagram mx-3"></i>
          <i className="fab fa-youtube"></i>
         </div>
         {
           props.userData ? <>
            <li className="nav-item">
          <samp onClick={props.logOut} className="nav-link">Logout</samp>
         </li>
           </>:
           <>
            <li className="nav-item">
          <Link className="nav-link" to="login">Login</Link>
         </li>
         <li className="nav-item">
          <Link className="nav-link" to="regiser">Register</Link>
         </li>
        
           </>
         }
      
       </ul>
    </div>
  </div>
</nav>
    )
} 