import axios from "axios";
import React, { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";

export default function Details() {
    let beseImgUrl='https://image.tmdb.org/t/p/original/';
    let [searchParam, setSerchParam] = useSearchParams();
    let [details,setDetails]=useState({})
    let currentId = searchParam.get("id");
    async function getDetails(){
          let { date } = await axios.get(
              `https://api.themoviedb.org/3/movie/${currentId}?api_key=6f55f87841085a94de7e2f6c0b8beb25&language=en-US`
          );
          setDetails.log(date)
    }
    useEffect(() => {
        getDetails();
    }, []);
    return (
        <div className="row">
          <div className="col-md-4">
            <img className="w-100" src={beseImgUrl + details.poster_path} alt="" />
          </div>
          <div className="col-md-8">
              <h6>{details.overiew}</h6>
          </div>
        </div>
    );
}