import axios from "axios";
import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import styles from './Home.module.css' 

export default function Home(){
    let [trendingMovies,setTrendingMovies]=useState([]);
    let [trendingTvShows,setTrendingTvShows]=useState([]);
    let [trendingPeople,setTrendingPeople]=useState([]);
    let beseImgUrl='https://image.tmdb.org/t/p/original/';
    async function geytTrendingItems(mediaType,callbeck){
        let {data}=await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/day?api_key=6f55f87841085a94de7e2f6c0b8beb25`)
        callbeck(data.results);
    }
     useEffect(()=>{
         geytTrendingItems('movie',setTrendingMovies);
         geytTrendingItems('tv',setTrendingTvShows);
         geytTrendingItems('person',setTrendingPeople);
    },[])
    let navigate=useNavigate();
    function goToDetails(id){
    navigate({
        pathname:'/details',
        search:`?id=${id}`
    })
    }
return(
 
    <>
    <div className="row">
        <dir className="col-md-4 d-flex align-items-center ">
          <div className="w-100">
          <div className={`w-25 ${styles.brdr} mb-3`}></div>
            <h2>Trending</h2>
            <h2>Movies</h2>
            <h2>To watch nom</h2>
            <p className="secondColor mb-3">Most watched movies by day</p>
            <div className={`w-100 ${styles.brdr} mb-3`}></div>
          </div>
        </dir>
        {trendingMovies.map((movie,index)=>
        <div onClick={()=>goToDetails(movie.id)} className="col-md-2 my-3"key={index}>
            <dir>
                <img className="w-100 mb-2" src={beseImgUrl+ movie.poster_path } alt="" />
                <h5>{movie.title}</h5>
            </dir>
        </div>
        )}
    </div>
    <div className="row">
        <dir className="col-md-4 d-flex align-items-center ">
          <div className="w-100">
          <div className={`w-25 ${styles.brdr} mb-3`}></div>
            <h2>Trending</h2>
            <h2>Tv shows</h2>
            <h2>To watch nom</h2>
            <p className="secondColor mb-3">Most watched tv shows by day</p>
            <div className={`w-100 ${styles.brdr} mb-3`}></div>
          </div>
        </dir>
        {trendingTvShows.map((tv,index)=>
        <div className="col-md-2 my-3"key={index}>
            <dir>
                <img className="w-100 mb-2" src={beseImgUrl+ tv .poster_path } alt="" />
                <h5>{tv.name}</h5>
            </dir>
        </div>
        )}
    </div>
    <div className="row">
        <dir className="col-md-4 d-flex align-items-center ">
          <div className="w-100">
          <div className={`w-25 ${styles.brdr} mb-3`}></div>
            <h2>Trending</h2>
            <h2>People</h2>
            <h2>To watch nom</h2>
            <p className="secondColor mb-3">Most watched People by day</p>
            <div className={`w-100 ${styles.brdr} mb-3`}></div>
          </div>
        </dir>
        {trendingPeople.map((person,index)=>
        <div className="col-md-2 my-3"key={index}>
            <dir>
                <img className="w-100 mb-2" src={beseImgUrl+ person .profile_path } alt="" />
                <h5>{person.name}</h5>
            </dir>
        </div>
        )}
    </div>
    </>

    )
}